@mixin resizeComponent {

	.resizable {
		&-none {
			resize: none; }

		@each $resizableLabel in $resizableLabels {
			$i: index($resizableLabels, $resizableLabel);
			&-#{$resizableLabel} {
				resize: nth($resizableValues, $i); } } }

	.not-resizable {
		@extend .resizable-none; } }
