@mixin overflowComponent {

	.#{$breakpointPrefix}overflow {
		&-x {
			&-hidden {
				overflow-x: hidden; }

			&-auto {
				overflow-x: auto; } }

		&-y {
			&-hidden {
				overflow-y: hidden; }

			&-auto {
				overflow-y: auto; } }

		&-auto {
			@extend .#{$breakpointPrefix}overflow-x-auto, .#{$breakpointPrefix}overflow-y-auto; }

		&-hidden {
			@extend .#{$breakpointPrefix}overflow-x-hidden, .#{$breakpointPrefix}overflow-y-hidden; } } }
