@mixin borderRadiusComponent {

	.square,
	.radius-none {
		border-radius: 0; }

	@for $i from 1 through 5 {
		.rounded-#{$i},
		.radius-#{$i} {
			border-radius: $i * 2px; } }

	.circle {
		border-radius: 100%; } }
