@mixin resetsComponent {

	.margin-none {
		margin: 0; }

	.padding-none {
		padding: 0; }

	.border-none {
		border: 0; }

	.outline-none {
		outline: 0; }

	.shadow-none {
		box-shadow: none; }

	.width-auto {
		width: auto; }

	.min-width-unset {
		min-width: unset; }

	.selection-disabled {
		user-select: none; } }
