@mixin textComponent {

	.#{$breakpointPrefix}text {
		@if $breakpointPrefix == null {

			&-undecorated {
				text-decoration: none; }

			&-untransformed {
				text-transform: none; }

			&-unstyled {
				font-style: normal; }

			&-normal {
				@extend .text-undecorated;
				@extend .text-untransformed;
				@extend .text-unstyled;
				@extend .text-normal-weight; }

			// Weight
			&-normal-weight {
				font-weight: normal; }

			&-lighter {
				font-weight: lighter; }

			&-bold {
				font-weight: bold; }

			// Style
			&-italic {
				font-style: italic; }

			&-oblique {
				font-style: oblique; }

			// Decoration
			&-underline {
				text-decoration: underline; }

			&-overline {
				text-decoration: overline; }

			&-strikethrough {
				text-decoration: line-through; }

			// Transform
			&-uppercase {
				text-transform: uppercase; }

			&-lowercase {
				text-transform: lowercase; }

			&-capitalize {
				text-transform: capitalize; }

			&-nowrap {
				white-space: nowrap; } }

		// Alignment
		&-centered {
			@include textAlignment(center, centered); }

		&-left {
			@include textAlignment(left); }

		&-right {
			@include textAlignment(right); }

		&-justify {
			@include textAlignment(justify); } }

	@if $breakpointPrefix == null {
		.cutted-text {
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap; }

		// Words
		.break {
			&-words {
				word-wrap: break-word; }

			&-all {
				word-break: break-all; } }

		.keep-all {
			word-break: keep-all; } } }
