@mixin colorComponent {

	.color {
		&-inherit {
			color: inherit; }

		&-white {
			color: $color_1; }

		&-gray {
			color: $color_2; }

		&-black {
			color: $color_3; }

		&-green {
			color: $color_8; }

		&-orange {
			color: $color_6; }

		&-red {
			color: $color_4; } } }
